
.post-like-bar-container {
  height: 80px;
}

.post-like-bar {
  display: flex;
  justify-content: space-between;
  justify-items: space-between;
  width: 95%;
  padding: 20px;
  font-size: 40px;
}

.post-like-bar-left {
  width: 50px;
}

.post-like-bar-right {
  width: 50px;
}

.star-icon {
  color: white;
}

.post-like-bar-left:hover,
.post-like-bar-right:hover {
  cursor: pointer;
}

.active {
  color: gold;
}

@media only screen and (min-width:800px) {

  .post-like-bar-left:hover .star-icon {
    position: relative;
    right: 5px;
    bottom: 5px;
    color: gold;
  }

  .post-like-bar-right:hover .download-icon {
    position: relative;
    right: 5px;
    bottom: 5px;
  }

  .post-like-bar-left:hover,
  .post-like-bar-right:hover {
  font-size: 50px;
}
}